<template>
  <div id="edit" class="container">
    <h1 class="page-header">编辑用户</h1>
    <form v-on:submit="editCustomer">
      <div class="well">
        <h4>用户信息</h4>
        <div class="form-group">
          <label>姓名</label>
          <input type="text" placeholder="姓名" class="form-control" v-model="customer.name" required>
        </div>
        <div class="form-group">
          <label>电话</label>
          <input type="tel" placeholder="电话" class="form-control" v-model="customer.phone" required>
        </div>
        <div class="form-group">
          <label>邮箱</label>
          <input type="email" placeholder="邮箱" class="form-control" v-model="customer.email" required>
        </div>
        <div class="form-group">
          <label>学历</label>
          <input type="text" placeholder="学历" class="form-control" v-model="customer.education">
        </div>
        <div class="form-group">
          <label>毕业学校</label>
          <input type="text" placeholder="毕业学校" class="form-control" v-model="customer.graduatedSchool">
        </div>
        <div class="form-group">
          <label>职业</label>
          <input type="text" placeholder="职业" class="form-control" v-model="customer.profession">
        </div>
        <div class="form-group">
          <label>个人简介</label>
          <textarea placeholder="个人简介" class="form-control" v-model="customer.profile"></textarea>
        </div>
        <button type="submit" class="btn btn-default btn-primary">提交</button>
      </div>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "Edit",
    data() {
      return {
        customer: {
          name: '',
          email: '',
          phone: '',
          education: '',
          graduatedSchool: '',
          profession: '',
          profile: ''
        }
      }
    },
    created(){
      axios.get('/users/'+this.$route.params.id).then((data)=>{
        this.customer=data.data;
      })
    },
    methods: {
      addCustomer(e) {
        let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (!myreg.test(this.customer.phone)) {
          alert("请输入正确的手机号！");
          e.preventDefault();
        } else {
          axios.post("/users", this.customer).then(() => {
            this.$router.push({path: "/", query: {alert: "用户信息添加成功！"}});
          });
          e.preventDefault();
        }

      }
    }
  }
</script>

<style scoped type="text/less" lang="less">
  #edit {
    textarea {
      height: 100px;
    }
  }
</style>



