<template>
    <div id="customers" class="container">
      <Alert v-if="alert" :message="alert"></Alert>
      <h1 class="page-header">用户管理系统</h1>
      <input class="search form-control" type="text" placeholder="搜索" v-model="filterCustomer">
      <table class="table table-striped">
        <thead>
        <tr>
          <th>姓名</th>
          <th>电话</th>
          <th>邮箱</th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="user in filterCustomers(customers,filterCustomer)">
          <td>{{user.name}}</td>
          <td>{{user.phone}}</td>
          <td>{{user.email}}</td>
          <td><router-link class="btn btn-default btn-sm" :to="'/customer/'+user.id">详情</router-link></td>
        </tr></tbody>
      </table>
    </div>
</template>

<script>
  import Alert from './Alert'
  import axios from 'axios'
    export default {
        name: "customers",
      data(){
          return{
            customers:[],
            alert:'',
            filterCustomer:''
          }
      },
      components:{
          Alert
      },
      created(){
          axios.get('/users').then((data)=>{
            this.customers=data.data;
            this.alert=this.$route.query.alert
          })
      },
      methods:{
        filterCustomers(customers,value){
          return customers.filter(function (customer) {
            return customer.name.match(value);
          })
        }
      }
      // updated(){
      //   this.alert=this.$route.query.alert
      // },
      // methods:{
      //     getCustomers(){
      //       axios.get('/users').then((data)=>{
      //         this.customers=data.data;
      //       })
      //     }
      // }
    }
</script>

<style scoped>
.search{
  margin-bottom: 20px;
}
</style>



