<template>
  <div id="details" class="container">
    <router-link to="/" class="btn btn-default">返回</router-link>
    <h1 class="page-header">{{customer.name}}
      <button @click="deleteCustomer(customer.id)" class="del btn btn-danger pull-right ">删除</button>
      <router-link :to="'/edit/'+customer.id" class="btn btn-primary pull-right">编辑</router-link>
    </h1>
    <ul class="list-group">
      <li class="list-group-item"><span class="glyphicon glyphicon-phone"></span>{{customer.phone}}</li>
      <li class="list-group-item"><span class="glyphicon glyphicon-globe"></span>{{customer.email}}</li>
      <li v-if="customer.education" class="list-group-item"><span class="glyphicon glyphicon-list-alt"></span>{{customer.education}}</li>
      <li v-if="customer.graduatedSchool" class="list-group-item"><span class="glyphicon glyphicon-education"></span>{{customer.graduatedSchool}}</li>
      <li v-if="customer.profession" class="list-group-item"><span class="glyphicon glyphicon-bookmark"></span>{{customer.profession}}</li>
      <li v-if="customer.profile" class="list-group-item"><span class="glyphicon glyphicon-thumbs-up"></span>{{customer.profile}}</li>
    </ul>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "Details",
    data(){
      return{
        customer:{}
      }
    },
    created() {
      this.getCustomer(this.$route.params.id)
    },
    methods:{
      getCustomer(id){
        axios.get('/users/'+id).then((data) => {
          this.customer = data.data;
        })
      },
      deleteCustomer(id){
        axios.delete('/users/'+id).then(()=>{
          this.$router.push({path:'/',query:{alert:"删除用户信息成功！"}})
        })
      }
    }
  }
</script>

<style scoped type="text/less" lang="less">
#details{
  .del{
    margin-left: 20px;
  }
  ul{
    li{
      span{
        display: inline-block;
        margin-right: 20px;
      }
    }
  }
}
</style>



